/* table */
.table_container {
  padding: 0 16px;
  margin-bottom: 40px;
  position: relative;
}
.table_container .all_btn {
  position: absolute;
  top: 0;
  right: 16px;
}
.table_container .all_btn a {
  font-size: 13px;
  text-decoration: none;
  color: black;
}
.table_container table {
  width: 100%;
  margin-top: 25px;
  border-spacing: 0;
  font-size: 15px;
}
.table_container table.main {
  display: block;
  height: 200px;
  overflow: hidden;
}
.table_container th {
  height: 25px;
}
.table_container table tbody tr:hover .title {
  text-decoration: underline;
}
.table_container table tbody tr td:first-child {
  padding: 5px 5px;
}
.table_container table tbody tr td:last-child {
  width: 100px;
  display: block;
  text-align: right;
  padding-right: 5px;
}
.table_container table tbody tr .category {
  font-size: 13px;
  text-align: center;
}
.table_container table tbody tr .title {
  width: 100%;
  cursor: pointer;
}
.table_container td {
  padding: 5px;
  height: 30px;
  line-height: 30px;
}
.table_container tbody tr:nth-child(odd) {
  background-color: #fff;
}
